import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, Slider, Input, FormLayout} from '@vkontakte/vkui';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

//TODO:Добавить картинку и изменение размеров на ней. Проставить округление на выводимых значениях размеров.

class SizeSelection extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			value1: 24,
			value2: 24,
		}
		this.go = this.props.go.bind(this);
	}
	render() {
		return (
			<Panel id={this.props.id} style={{overflow: 'hidden'}}>
				<PanelHeader>
					ВЫБЕРИТЕ <strong>РАЗМЕР</strong>
				</PanelHeader>
				<Group className="Group">
					<FormLayout>
		                <Slider
		                    min={10}
		                    max={30}
		                    value={Number(this.state.value1)}
		                    onChange={value1 => this.setState({value1})}
		                    top="Ширина, мм [10, 30]"
		                />
		                <Input value={String(this.state.value1)} onChange={e => this.setState({ value1: e.target.value })} type="number"/>
		                <Slider
		                    min={10}
		                    max={30}
		                    value={Number(this.state.value2)}
		                    onChange={value2 => this.setState({value2})}
		                    top="Длина, мм [10, 30]"
		                />
		                <Input value={String(this.state.value2)} onChange={e => this.setState({ value2: e.target.value })} type="number"/>
		            </FormLayout>
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>} 
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="shapeSelection"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>} 
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="fontSelection"
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>
		)
	}
}

SizeSelection.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default SizeSelection;
