import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, Input, Cell, List, InfoRow} from '@vkontakte/vkui';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';
import Adventure from '../fonts/Adventure_2.ttf'

class FontSelection extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			textValue: "",
		}
		this.go = this.props.go.bind(this);
	}
	handleImput = (e) => {
		this.setState({
			textValue: e.target.value
		})
	}

	render() {
		return (
			<Panel id={this.props.id} style={{overflow: 'hidden'}}>
				<PanelHeader>
					ВЫБЕРИТЕ <strong>ШРИФТ</strong>
				</PanelHeader>
					<Group className="Group">
						<FixedLayout vertical="top">
							<List>
								<Input
									top="Имя" 
									placeholder="Введите текст"
									alignment="center"
									onChange={this.handleImput}
								/>
							</List>	
						</FixedLayout>
						<InfoRow title="Шрифт: |#1">
							<Cell multiline>
								<Div style={{FontFamily: Adventure}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow title="Шрифт: |#2">
							<Cell multiline>
								<Div style={{FontFamily: Adventure}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow title="Шрифт: |#3">
							<Cell multiline>
								<Div style={{FontFamily: Adventure}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<FixedLayout vertical="bottom">
							<Div style={{display: 'flex'}}>
								<Button 
									className="Button" 
									size="xl"
									after={<img src={onLeft} alt="onLeft"/>}
									stretched 
									level="2" 
									onClick={this.go} 
									data-to="shapeSelection"
								></Button>
								<Button 
									className="Button" 
									size="xl"
									after={<img src={onRight} alt="onRight"/>}
									stretched 
									level="2" 
									onClick={this.go} 
									data-to="textColorSelection"
								></Button>
							</Div>
						</FixedLayout>
					</Group>
			</Panel>
		);
	}
}

FontSelection.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default FontSelection;
