import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, HorizontalScroll} from '@vkontakte/vkui';
import '../style.css';
import bumYarlik from '../img/bumYarlik.svg';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

const itemStyle = {
    flexShrink: 0,
    width: 80,
    height: 94,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
	fontSize: 12
  };

class AddingPictures extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
		}
		this.go = this.props.go.bind(this);
	}
	render() {
		return (
			<Panel id={this.props.id} style={{overflow: 'hidden'}}>
				<PanelHeader>
					ДОБАВЬТЕ <strong>КАРТИНКУ</strong>
				</PanelHeader>
				<Group className="Group">
				Делаем диалог выбора картинки из галереи или загрузку файла. File.
				и еще ниже делаем пояснение
					<HorizontalScroll className="HorizontalScroll">
		        	  <div style={{ display: 'flex'}}>
		        	    <div style={{ ...itemStyle, paddingLeft: 4 }}>
							<Button before={<img src={bumYarlik} alt="bumYarlik"/>}></Button>
						</div>
		        	    <div style={itemStyle}>
							<Button before={<img src={bumYarlik} alt="bumYarlik"/>}></Button>
		        	    </div>
		        	    <div style={itemStyle}>
							<Button before={<img src={bumYarlik} alt="bumYarlik"/>}></Button>
		        	    </div>
						<div style={{ ...itemStyle, paddingLeft: 4 }}>
							<Button before={<img src={bumYarlik} alt="bumYarlik"/>}></Button>
						</div>
		        	    <div style={itemStyle}>
							<Button before={<img src={bumYarlik} alt="bumYarlik"/>}></Button>
		        	    </div>
		        	    <div style={itemStyle}>
							<Button before={<img src={bumYarlik} alt="bumYarlik"/>}></Button>
		        	    </div>
						<div style={{ ...itemStyle, paddingLeft: 4 }}>
							<Button before={<img src={bumYarlik} alt="bumYarlik"/>}></Button>
						</div>
		        	</div>
		        	</HorizontalScroll>

				<p>Помните, что четкость печати напрямую зависит от качества изображения</p>
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>} 
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="textColorSelection"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>} 
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="backgroundColorSelection"
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>
		);
	}
}

AddingPictures.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default AddingPictures;
