import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, List, Cell} from '@vkontakte/vkui';
import '../style.css';
import Nadstrochnaya from '../img/Nadstrochnaya.png';
import Flag from '../img/Flag.png';
import loop from '../img/loop.png';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';


//TODO:После нужно будет прижать поле с текстом по низу экрана, что бы не скакало и выглядело эстетичнее. 
//Так же добавить ему собственный фон. Расставить Отступы на кнопках, что бы не сливались.
//списками и стейтом сделать генерацию большого количества кнопок. 
//Что бы потом можно было нормально добавлять просто меняя стейт

class ViewSelection extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			buttonId: null,
		}
		this.setButton = this.setButton.bind(this);
		this.go = this.props.go.bind(this);
	}
	setButton(id){
        this.setState({buttonId: id});
	}
	render() {
		return(
			<Panel id={this.props.id} style={{overflow: 'hidden'}}>
				<PanelHeader>
					ВЫБЕРИТЕ <strong>ВИД</strong>
				</PanelHeader>
				<Group className="Group">
					<Div>
						<Div>
							<Button className={this.state.buttonId === 1?
								"logoButton" : "Button"} 
								size="xl"
								stretched
								level="2" 
								onClick={() => this.setButton(1)}
							>НАДСТРОЧНАЯ</Button>
							<Button className={this.state.buttonId === 2?
								"logoButton" : "Button"} 
								size="xl"
								stretched
								level="2"
								onClick={() => this.setButton(2)}
							>ФЛАЖОК</Button>
							<Button className={this.state.buttonId === 3?
								"logoButton" : "Button"} 
								size="xl"
								stretched
								level="2"
								onClick={() => this.setButton(3)}
							>ПЕТЛЯ</Button>
						</Div>
						<List>
							<Cell multiline>
								{this.state.buttonId === 1 && <img src={Nadstrochnaya} alt="Nadstrochnaya"/>}
								{this.state.buttonId === 2 && <img src={Flag} alt="Flag"/>}
								{this.state.buttonId === 3 && <img src={loop} alt="loop"/>}
								{this.state.buttonId !== 1  &&
									this.state.buttonId !== 2 &&
									this.state.buttonId !== 3 &&
									this.state.buttonId !== 4 && <Div style={{backgroundColor: '#ffffff'}} >Выбери свой вариант исполнения</Div>}
							</Cell>
						</List>
					</Div>
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>}
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="materialSelection"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>}
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="shapeSelection"
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>
		);
	}
}

ViewSelection.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default ViewSelection;
