import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout} from '@vkontakte/vkui';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

// Человек залогинился. Нужно обратиться к нему по ФИО и уже потом говорить, что мы предлагаем.
//			<Group>
//				<ListItem
//					before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
//				>
//					{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
//				</ListItem>
//			</Group>}

class WelcomeScreen extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
		}
		this.go = this.props.go.bind(this);
	}

	render() {
		return(
			<Panel id={this.props.id} style={{overflow: 'hidden'}}>
				<PanelHeader>
					О КОНСТРУКТОРЕ
				</PanelHeader>
				<Group className="Group">
					<Div style={{ textAlign: "center"}}>Здравствуйте. 

					<p>Мы предлагаем Вам самостоятельно создать макет бирки для печати. Если у вас возникли вопросы, то
    		        напишите нам, и мы с радостью ответим.</p></Div>
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button"
								size="xl" 
								after={<img src={onLeft} alt="onLeft"/>} 
								stretched level="2" 
								onClick={this.go} 
								data-to="home"
								>
							</Button>
							<Button 
								className="Button"
								size="xl" 
								after={<img src={onRight} alt="onRight"/>} 
								stretched level="2" 
								onClick={this.go} 
								data-to="typeSelection"
								>
							</Button>
						</Div>	
    		  	    </FixedLayout>
				</Group>
			</Panel>
		);
	}
}

WelcomeScreen.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default WelcomeScreen;
