import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, List, Cell} from '@vkontakte/vkui';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

//TODO:После нужно будет прижать поле с текстом по низу экрана, что бы не скакало и выглядело эстетичнее. 
//Так же добавить ему собственный фон. Расставить Отступы на кнопках, что бы не сливались.
//списками и стейтом сделать генерацию большого количества кнопок. 
//Что бы потом можно было нормально добавлять просто меняя стейт

class MaterialSelection extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			buttonId: null
		}
		this.setButton = this.setButton.bind(this);
		this.go = this.props.go.bind(this);
	}
    setButton(id){
		this.setState({buttonId: id});
	}
	render() {
		return(
			<Panel id={this.props.id} style={{overflow: 'hidden'}}>
				<PanelHeader>
					ВЫБЕРИТЕ <strong>МАТЕРИАЛ</strong>
				</PanelHeader>
				<Group className="Group">
        	    	<Div>
        	    	    <Button className={this.state.buttonId === 1?
        	    	        "logoButton" : "Button"} 
        	    	        size="xl"
        	    	        stretched
        	    	        level="2" 
        	    	        onClick={() => this.setButton(1)}
        	    	    >САТИН</Button>
        	    	    <Button className={this.state.buttonId === 2?
        	    	        "logoButton" : "Button"} 
        	    	        stretched
        	    	        size="xl"
        	    	        level="2"
        	    	        onClick={() => this.setButton(2)}
        	    	    >РЕПС</Button>
        	    	    <Button className={this.state.buttonId === 3?
        	    	        "logoButton" : "Button"} 
        	    	        size="xl"
        	    	        stretched
        	    	        level="2"
        	    	        onClick={() => this.setButton(3)}
        	    	    >ХЛОПОК</Button>
        	    	    <Button className={this.state.buttonId === 4?
        	    	        "logoButton" : "Button"} 
        	    	        size="xl"
        	    	        stretched
        	    	        level="2"
        	    	        onClick={() => this.setButton(4)}
        	    	    >СИЛИКОН</Button>
        	    	    <Button className={this.state.buttonId === 5?
        	    	        "logoButton" : "Button"} 
        	    	        size="xl"
        	    	        stretched
        	    	        level="2"
        	    	        onClick={() => this.setButton(5)}
        	    	    >КОЖКАРТОН</Button>
        	    	    <Button className={this.state.buttonId === 6?
        	    	        "logoButton" : "Button"} 
        	    	        size="xl"
        	    	        stretched
        	    	        level="2"
        	    	        onClick={() => this.setButton(6)}
        	    	    >ПОЛИХЛОПОК</Button>
					</Div>
        	        <List>
        	            <Cell multiline>
        	                {this.state.buttonId === 1 && <Div style={{backgroundColor: '#ffffff'}} >Сатин <hr/>Тонкий, мягкий, с лёгким блеском. Отлично передаёт четкие детали изображения, печать очень стойкая.</Div>}
        	                {this.state.buttonId === 2 && <Div style={{backgroundColor: '#ffffff'}} >Репс <hr/>Плотный, рубчатый, фактурный материал, печать очень стойкая.</Div>}
        	                {this.state.buttonId === 3 && <Div style={{backgroundColor: '#ffffff'}} >Силикон <hr/>Полупрозрачный, матовый и очень надежный материал, печать может быть только черного, синего, красного, зеленого, розового, золотого и серебряного цвета.</Div>}
        	                {this.state.buttonId === 4 && <Div style={{backgroundColor: '#ffffff'}} >Хлопок <hr/>100% натуральный хлопок полотняного плетения, печать выдерживает до 20 стирок.</Div>}
        	                {this.state.buttonId === 5 && <Div style={{backgroundColor: '#ffffff'}} >Кожкартон <hr/> Печать лучше выполнять тёмными цветами, например, чёрным, красным или синим.</Div>}
        	                {this.state.buttonId === 6 && <Div style={{backgroundColor: '#ffffff'}} >Полихлопок <hr/> Имитация хлопка. Рубчатый, плотный, фактурный материал. 100% ПЭ. Устойчив к стирке.</Div>}
        	                {this.state.buttonId !== 1  &&
        	                    this.state.buttonId !== 2 &&
        	                    this.state.buttonId !== 3 &&
        	                    this.state.buttonId !== 4 &&
        	                    this.state.buttonId !== 5 &&
        	                    this.state.buttonId !== 6 && <Div style={{backgroundColor: '#ffffff'}} >Выбери свой вариант исполнения.</Div>}
        	            </Cell>
        	        </List>		
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>} 
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="typeSelection"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>}
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="viewSelection"
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>
		);
	}	
}

MaterialSelection.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default MaterialSelection;
