import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout} from '@vkontakte/vkui';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

function sendLayout(Arr) {
	console.log(Arr)
} 

class SendLayout extends React.Component {
	constructor(props) {
		super(props);
		this.state = {

		}
		this.go = this.props.go.bind(this);
	}
	render() {
		return (
			<Panel id={this.props.id} style={{overflow: 'hidden'}}>
				<PanelHeader>
					ОТПРАВКА ЗАКАЗА
				</PanelHeader>
				<Group className="Group">
					Вот сюда надо финальный скрипт воткнуть.
			
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>} 
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="backgroundColorSelection"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>} 
								stretched 
								level="2" 
								onClick={sendLayout}
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>
		);
	}
}

SendLayout.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.object.isRequired,
};

export default SendLayout;
