import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, List, Cell} from '@vkontakte/vkui';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';
import bumYarlik from '../img/bumYarlik.svg';
import nakleyka from '../img/nakleyka@4x.svg';
import prishivnaya from '../img/prishivnaya.svg';
import termo from '../img/termo@4x.svg';

//списками и стейтом сделать генерацию большого количества кнопок. 
//Что бы потом можно было нормально добавлять просто меняя стейт
//TODO:Прижать поле с текстом вниз. Расставить Отступы на кнопках, что бы не сливались.

class TypeSelection extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			buttonId: null
		}
		this.go = this.props.go.bind(this);
		this.setButton = this.setButton.bind(this);	
	}
	setButton(id){
        this.setState({buttonId: id});
	}
	render() {
		return(
			<Panel id={this.props.id} style={{overflow: 'hidden'}}>
				<PanelHeader>
					ВЫБЕРИТЕ <strong>ТИП</strong>
				</PanelHeader>
				<Group className="Group1">
					<Div>
			            <Button className={this.state.buttonId === 1?
			                    "logoButton" : "Button"}  
			                after={<img src={prishivnaya} alt="prishivnaya"/>}
			                size="xl"
			                stretched level="2"
			                onClick={() => this.setButton(1)}
			            >ПРИШИВНАЯ</Button>
			            <Button className={this.state.buttonId === 2?
			                    "logoButton" : "Button"}  
			                after={<img src={bumYarlik} alt="bumYarlik"/>}
			                size="xl"
			                stretched level="2"
			                onClick={() => this.setButton(2)}
			            >БУМАЖНЫЙ ЯРЛЫК</Button>
			            <Button className={this.state.buttonId === 3?
			                    "logoButton" : "Button"}  
			                after={<img src={termo} alt="termo"/>}
			                size="xl"
			                stretched level="2"
			                onClick={() => this.setButton(3)}
			            >ТЕРМО</Button>
			            <Button className={this.state.buttonId === 4?
			                    "logoButton" : "Button"}  
			                after={<img src={nakleyka} alt="nakleyka"/>}
			                size="xl"
			                stretched level="2"
			                onClick={() => this.setButton(4)}
			            >НАКЛЕЙКА</Button>
			            <List>
			                <Cell multiline>
			                    {this.state.buttonId === 1 && <Div style={{backgroundColor: '#ffffff'}} >Пришивная <hr/> Сатин, репс, силикон, хлопок, кожкартон.</Div>}
			                    {this.state.buttonId === 2 && <Div style={{backgroundColor: '#ffffff'}} >Бумажный ярлык <hr/> Белый или крафт-картон плотностью 250гр</Div>}
			                    {this.state.buttonId === 3 && <Div style={{backgroundColor: '#ffffff'}} >Термонаклейка <hr/> Термонаклейка на белом фоне, приклеивается утюгом, стойкая печать, несколько цветов на выбор</Div>}
			                    {this.state.buttonId === 4 && <Div style={{backgroundColor: '#ffffff'}} >Наклейка <hr/> Бумажная наклейка с полноцветной печатью, любой формы. Максимальный размер 9см</Div>}
			                    {this.state.buttonId !== 1  &&
			                        this.state.buttonId !== 2 &&
			                        this.state.buttonId !== 3 &&
			                        this.state.buttonId !== 4 && <Div style={{backgroundColor: '#ffffff'}} >Выбери свой вариант исполнения</Div>}
			                </Cell>
			            </List>
			        </Div>
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>}
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="welcomeScreen"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>} 
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="materialSelection"
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>	
		);
	}
}

TypeSelection.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default TypeSelection;
